body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.side-nav {
  width: 100%;

  padding: 20px;
  background: var(--color-three);
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  z-index: 199;
  transition: ease-out, -webkit-transform 0.5s;
  transition: transform 0.5s, ease-out;
  transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
}
.hide {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
#nav-link i {
  font-size: 20px;
}
.bar {
  position: fixed;
  z-index: 200;
  top: 10px;
  right: 20px;
}
.bar1,
.bar2,
.bar3 {
  width: 34px;
  height: 4px;
  background-color: var(--color-three);
  margin: 8px 0;
  border-bottom: solid 2px var(--color-one);
}

.change1 {
  -webkit-transform: rotate(-50deg) translate(-9px, 8px);
  transform: rotate(-50deg) translate(-9px, 8px);
  transition: ease-out, -webkit-transform 0.6s;
  transition: transform 0.6s, ease-out;
  transition: transform 0.6s, ease-out, -webkit-transform 0.6s;
}

.change2 {
  opacity: 0;
}

.change3 {
  -webkit-transform: rotate(50deg) translate(-9px, -8px);
  transform: rotate(50deg) translate(-9px, -8px);
  transition: ease-out, -webkit-transform 0.6s;
  transition: transform 0.6s, ease-out;
  transition: transform 0.6s, ease-out, -webkit-transform 0.6s;
}

.link-effect {
  -webkit-animation-name: grow-effect;
  -webkit-animation-duration: 2s;
  transition: all 0.2s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
